import React, { useState } from "react"
import { Link, useStaticQuery, graphql } from "gatsby"

import {
  CarouselProvider,
  DotGroup,
  Image,
  Slide,
  Slider,
} from "pure-react-carousel"

import s from "pure-react-carousel/dist/react-carousel.es.css"

export default function Carousel() {
  const [slide, setSlide] = useState(0)

  const data = useStaticQuery(graphql`
    {
      allWpSlider {
        edges {
          node {
            id
            uri
            sliderFields {
              link
              image {
                id
                sourceUrl
                altText
              }
            }
          }
        }
      }
    }
  `)
  console.log(data)
  const { edges } = data.allWpSlider

  return (
    <>
      {/* <pre>{JSON.stringify(edges, null, 2)}</pre>  */}

      <CarouselProvider
        className="xl:w-3/4"
        visibleSlides={1}
        totalSlides={2}
        step={2}
        naturalSlideWidth={400}
        naturalSlideHeight={300}
        hasMasterSpinner
        currentSlide={slide}
        isPlaying
      >
        <Slider>
          {edges.map((edge, index) => {
            const image = edge.node.sliderFields.image.sourceUrl
            const altText = edge.node.sliderFields.image.altText
            const link = edge.node.sliderFields.link
            // need to set up a helper function to modify links to paths
            console.log(link)
            const id = edge.node.id
            return (
              <>
                <Slide index={id}>
                  <Link to={link}>
                    <Image src={image} alt={altText} />
                  </Link>
                </Slide>
              </>
            )
          })}
        </Slider>
        {/* <ButtonPlay childrenPlaying="Pause" childrenPaused="Play" /> */}
        {/* <ButtonFirst>First</ButtonFirst>
        <ButtonBack>Back</ButtonBack>
        <ButtonNext>Next</ButtonNext>
        <ButtonLast>Last</ButtonLast> */}
        <DotGroup />
      </CarouselProvider>
    </>
  )
}
