import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"

import HeroHalfSlider from "../components/heros/hero-slider"
import Features from "../components/features/features-home"
import InfinityProducts from "../components/infinity-products"
import Team from "../components/team"
import Testimonials from "../components/testimonials"
import GetInTouch from "../components/forms/get-in-touch"
import FaqHVAC from "../components/faq/hvac-faq"
import SpecialsCTA from "../components/cta/specials-cta"

export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDozMDY=" }) {
      id
      title
      heroFields {
        en {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const IndexPage = ({ data }) => {
  const hero = data.wpHero.heroFields
  return (
    <Layout>
      <Translation>
        {t => (
          <>
            {i18n.language === "en" ? (
              <SEO title="Texas HVAC Company | Residential &amp; Commercial HVAC Contractors" />
            ) : (
              <SEO title="Compaa de HVAC en Texas | Contratadores de HVAC Residencial y Comercial" />
            )}
            <HeroHalfSlider
              headline={`${hero[i18n.language].headline}`}
              smallcta={`${hero[i18n.language].smallcta}`}
              subheadline={`${hero[i18n.language].subheadline}`}
              body={`${hero[i18n.language].body}`}
              image={`${hero[i18n.language].image.sourceUrl}`}
              button2Text={`${hero[i18n.language].buttontext}`}
              button2Link={
                hero[i18n.language].buttonlink !== null
                  ? `${hero[i18n.language].buttonlink}`
                  : `#getInTouch`
              }
            />
          </>
        )}
      </Translation>
      <Features />
      <SpecialsCTA />
      <InfinityProducts />
      <Team />
      <FaqHVAC />
      <Testimonials />
      <GetInTouch
        locationName="Air Rey A/C &amp; Heating"
        // address="5504 Bandera Rd. Suite 202"
        city="Serving Austin, Houston, Dallas, &amp; San Antonio"
        state=""
        zip=""
        phone="469-420-5155"
        email="sales@airreydfw.com"
      />
    </Layout>
  )
}

export default IndexPage